import React, { Fragment, useEffect } from "react";
import { Route, Switch } from "react-router-dom";
import { connect } from "react-redux";
import * as actions from "./store/actions";

import UserLayout from "./hoc/Layout/UserLayout";

import Dashboard from "./containers/Dashboard/Dashboard";
import Courses from "./containers/Courses/Courses";
import MyCourses from "./containers/Courses/MyCourses";
import Auth from "./containers/Auth/Auth";
import CourseDetail from "./components/CourseList/CourseDetail/CourseDetail";
import Logout from "./containers/Auth/Logout/Logout";
import UsersManager from "./containers/UsersManager/UsersManager";
import CoursesManager from "./containers/CoursesManager/CoursesManager";
import PageNotFound from "./containers/PageNotFound/PageNotFound";
import About from "./containers/Auth/About/About";

const RouteUser = ({ Component, ...props }) => {
  return (
    <Route
      {...props}
      render={() => (
        <UserLayout>
          <Component />
        </UserLayout>
      )}
    />
  );
};

const RouteAdmin = ({ Component, isAdmin, ...props }) => {
  return (
    <Route
      {...props}
      render={() =>
        isAdmin ? (
          <UserLayout>
            <Component />
          </UserLayout>
        ) : (
          <PageNotFound />
        )
      }
    />
  );
};

const App = ({ onTryAutoSignup }) => {
  const user = JSON.parse(localStorage.getItem("user"));
  const isAdmin = user && user.maLoaiNguoiDung === "GV";

  useEffect(() => {
    onTryAutoSignup();
  }, [onTryAutoSignup]);

  return (
    <Fragment>
      <Switch>
        {/* With Layout */}
        <RouteUser
          path={process.env.PUBLIC_URL + "/"}
          exact
          Component={Dashboard}
        />
        <RouteUser
          path={process.env.PUBLIC_URL + "/about"}
          exact
          Component={About}
        />
        <RouteUser
          path={process.env.PUBLIC_URL + "/courses"}
          exact
          Component={Courses}
        />
        <RouteUser
          path={process.env.PUBLIC_URL + "/my-courses"}
          exact
          Component={MyCourses}
        />
        <RouteUser
          path={process.env.PUBLIC_URL + "/courses/:id"}
          Component={CourseDetail}
        />
        <RouteUser
          path={process.env.PUBLIC_URL + "/logout"}
          Component={Logout}
        />
        <RouteAdmin
          path={process.env.PUBLIC_URL + "/users-management"}
          Component={UsersManager}
          isAdmin={isAdmin}
        />
        <RouteAdmin
          path={process.env.PUBLIC_URL + "/courses-management"}
          Component={CoursesManager}
          isAdmin={isAdmin}
        />
        {/* Without Layout */}
        <Route path="CourseHub/sign-in" component={Auth} />
        <Route path="CourseHub/sign-up" component={Auth} />
        {/* Page Not Found */}
        <Route path="" component={PageNotFound} />
      </Switch>
    </Fragment>
  );
};

const mapStateToProps = (state) => {
  return {
    isAuthenticated: state.auth.token !== null,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onTryAutoSignup: () => dispatch(actions.authCheckState()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
